import React, { useRef, useState } from 'react';
import Image from "next/image";
import { gsap } from "gsap";
import { useGSAP } from "@gsap/react";

const SectionRowImg = () => {
    const imageRefs = useRef([]);
    const containerRef = useRef(null);
    const timerRefs = useRef([]);

    const handleMouseEnter = (index, e) => {
        e.preventDefault();
        
        if (timerRefs.current[index]) {
            clearTimeout(timerRefs.current[index]);
        }
        
        timerRefs.current[index] = setTimeout(() => {
            const imageRef = imageRefs.current[index];
            if (imageRef) {
                gsap.to(imageRef, {
                    scaleY: 1,
                    duration: 1,
                    ease: "none",
                    transformOrigin: "bottom",
                });
            }
            timerRefs.current[index] = null;
        }, 100);
    };
    
    const handleMouseLeave = (index, e) => {
        e.preventDefault();
        if (timerRefs.current[index]) {
            clearTimeout(timerRefs.current[index]);
            timerRefs.current[index] = null;
        }
        
        const imageRef = imageRefs.current[index];
        if (imageRef) {
            gsap.to(imageRef, {
                scaleY: 0,
                duration: 1,
                ease: "none",
                transformOrigin: "top",
                onComplete: () => {
                    gsap.set(imageRef, {
                        transformOrigin: "bottom",
                    });
                }
            });
        }
    };

    useGSAP(() => {
        return () => {
            timerRefs.current.forEach(timer => {
                if (timer) clearTimeout(timer);
            });
        };
    }, { scope: containerRef });
    
    const items = [
        { src: "/chair-1.jpg", title: "our approach" },
        { src: "/chair-2.jpg", title: "our technology" },
        { src: "/chair-3.jpg", title: "our story" },
        { src: "/chair-4.jpg", title: "our design team" },
    ];

    return (
        <>
            <section 
                ref={containerRef}
                className="absolute top-0 left-0 h-full w-full grid lg:grid-cols-4 grid-cols-2"
            >
                {items.map(({ src, title }, index) => {
                    const [firstWord, ...rest] = title.split(" ");

                    return (
                        <div
                            key={index}
                            className="relative h-[50vh] w-[50vw] lg:w-[25vw] lg:h-full border-r border-gray-300 last:border-r-0"
                            // onMouseEnter={(e) => handleMouseEnter(index, e)}
                            // onMouseLeave={(e) => handleMouseLeave(index, e)}
                        >
                            <div 
                                className="absolute h-full w-full overflow-hidden img-wrapper scale-y-0 origin-bottom"
                                ref={el => (imageRefs.current[index] = el)}
                            >
                                <Image
                                    src={src}
                                    alt={title}
                                    fill
                                    style={{ objectFit: "cover" }}
                                    className="h-full w-full"
                                />
                            </div>
                            <h1 className="absolute z-1 bottom-0 left-0 p-6 md:p-4 font-light text-lg lg:text-4xl uppercase">
                                <span className="block mb-2">{firstWord}</span>
                                {rest.join(" ")}
                            </h1>
                        </div>
                    );
                })}
            </section>
            <section className="absolute z-3 bg-transparent top-0 left-0 h-full w-full grid lg:grid-cols-4 grid-cols-2">
                {items.map(({src, title},index) => (
                    <div 
                        key={index} 
                        className="relative h-[50vh] w-[50vw] lg:w-[25vw] lg:h-full" 
                        onMouseEnter={(e) => handleMouseEnter(index, e)}
                        onMouseLeave={(e) => handleMouseLeave(index, e)}
                    />
                ))}
            </section>
        </>
    );
};

export default SectionRowImg;